@media only screen and (min-width: 768px) {
  .content:before {
    content: "";
    position: fixed;
    left: 0;
    right: 0;
    z-index: -1;
    
    background-image: url("/images/BramTankink.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    
    -webkit-filter: blur(2px);
    -moz-filter: blur(2px);
    -o-filter: blur(2px);
    -ms-filter: blur(2px);
    filter: blur(2px);
  }
  
  .content {
    position: fixed;
    left: 0;
    right: 0;
    z-index: 0;
  }
}

body {
  margin: 0;
}
  
div.pageContainer{
  margin-left: 0;
  margin-right: 0;
  padding-top: 5vh;
}

@media only screen and (min-width: 768px) {
  div.pageContainer {
    margin-left: 10vw;
    margin-right: 10vw;
  }
}
div.navbar{
    display: flex;
    margin: 0;
    background-color: #444;
    height: 5vh;
    width: 100vw;
    z-index: 999;
    position: absolute;
    box-shadow: 0px 10px 10px 0px rgba(136,136,136,0.3);
}
div.navbar ul{
    margin-left: auto;
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: inline-block;
}
div.homepageContainer{
    padding: 0px;
    margin: 10vh;
    margin-left: 10vw;
    margin-right: 10vw;
}

div.formsAndMore{
    background-color: rgba(230,225,220,0.98);
    padding: 10px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    border-top-style: solid;
    border-top-width: 1vh;
    border-top-color: rgb(70, 130, 190);
    margin-top: -1vh;
    box-shadow: 0px 10px 10px 0px rgba(47, 47, 47, 0.6);
    height: 50vh;
}

button.formTab{
    background-color: rgba(195,190,185,0.98);
    border: none;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    color: #555;
    padding: 5px;
    font-size: 12px;
    font-weight: bold;
    min-height: 4vh;
    min-width: 6vw;
    z-index: 10;
}

button.formTab:hover{
    background-color: rgba(185,180,175,0.98);
}

button.formTab.active{
    background-color: rgba(230,225,220,0.98);
    color: #333;
}

input.form-control, textarea{
    background:none;
    border-color: #bbaaaa;
    border-radius: 3px;
    border-style: solid;
    border-width: 1px;
    min-height: 4vh;
    width: 100%;
    min-width: 200px;
    max-width: 300px;
    font-size: 14px;
}

div.inputContainer{
    margin-left: 1vw;
    margin-right: 1vw;
    margin-top: 2vh;
    margin-bottom: 2vh;
}

div.formButtonContainer{
    margin-left: 1vw;
    padding-top: 1vh;
}

button.loginButton{
    border-radius: 6px;
    border-style: none;
    background-color: rgb(70, 130, 190);
    font-weight: bold;
    font-size: 12px;
    height: 35px;
    width: 9vw;
    color: white;
}
div.container{
    display: flex;
    margin: 0;
}
div.ridertablecontainer{
    width: 50%;
}
div.usertablecontainer{
    width: 50%;
}
div.adminpageContainer{
    width: 100%;
    height: 95vh;
    background: #45484d; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #45484d 0%,#000000 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

textarea.queryInputBox{
    font-size: 10px;
    background: black;
    color: white;
}

div.outputTableContainer{
    height: 60vh;
    overflow: scroll;
}

table.outputTable{
    font-size: 10px;
    background: black;
    color: white;
}
